import React, { useState, useEffect } from 'react';
import axios from 'axios'
import './ItemMng.css';

function ItemMng(props) {

    const [ testStr, setTestStr ] = useState('');
    const[pageNum, setPageNum] = useState(0);
    const[itemMngCount, setItemMngCount] = useState(0)

    //  
    function callback(str) {
    setTestStr(str);
    }

    
    useEffect(
        () => {
         axios({
            url: '/item/itemMng',
            method: 'GET',
            headers: { 
                'Authorization' : 'Bearer ' + localStorage.getItem("ACCESS_TOKEN"),
                'Content-Type': 'application/json' 
              },
            params: {
                "pageNum": pageNum
            }
        }).then((res) => {
            callback(res.data);
            setItemMngCount(res.data.itemMngCount);
            if(res.data.problem != false) {
                alert("     .");
            }
        }).catch((error) => {
            if(error.response.status === 403 || error.response.status === 400) {
                alert("  .")
                window.location.href = "/members/login"
            }
        })
        }, []
); 

let arr = []
for(let i = 0 ; i < Math.ceil(itemMngCount/10) ; i++) {
    arr.push(i+1)
}

    const prevPage = (e) => {
        e.preventDefault();
        if (pageNum === 0) {
            alert("  .")
        } else {
            setPageNum(pageNum - 1);
            axios({
                url: '/item/itemMng',
                method: 'GET',
                params: {
                    "pageNum": pageNum-1
                }
            }).then((res) => {
                callback(res.data);
            })
        }
        
    }

    const nextPage = (pageNum, e) => {
        e.preventDefault();
        if(pageNum >= Math.ceil(itemMngCount/10) - 1) {
            alert(" .")
        } else {
            setPageNum(pageNum + 1);
            axios({
                url: '/item/itemMng',
                method: 'GET',
                params: {
                    "pageNum": pageNum+1
                }
            }).then((res) => {
                callback(res.data);
            })
        }
    }

    const clickPage = (arr, e) => {
        e.preventDefault();
        setPageNum(arr)
        axios({
            url: '/item/itemMng',
            method: 'GET',
            params: {
                "pageNum": arr
            }
        }).then((res) => {
            callback(res.data);
        })
    }


//  
    const[search, setSearch] = useState('')
    const handleSearchChange = (event) => {
        setSearch(event.target.value);
    }

    //  
    const [searchSellStatus, setSearchSellStatus] = useState('');
    const HandleOptionOrderState = (e) => {
        setSearchSellStatus(e.target.value);
    }


    const handleSearchClick = (e) => {
        e.preventDefault();
        axios({
            url: '/item/search',
            method: 'GET',
            params: {
                "searchName": search,
                "searchStatus": searchSellStatus
            }
        }).then((res) => {
            callback(res.data);
            if(res.data.problem != false) {
                alert("    .")
            }
        })
    }



        return(
            <div className="containerItemMng">


            <form role="form" method="get" className='itemMngForm'>
                <table className="table">
                    <thead>
                    <tr>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                    </tr>
                    </thead>

                    <tbody> 
                    {testStr.item && testStr.item.map((item) => {
                <li key={item.id}/>
                return(
                        <tr>
                            <td >{item.id}</td>
                            <td>
                                <a href={'/item/update/'+item.id}>{item.name}</a>
                            </td>
                            <td>{item.stockNumber}</td>
                            <td>{item.sellStatus}</td>
                            <td>{item.createdBy}</td>
                            <td>{item.regTime}</td>
                        </tr>
                )
            })}
                    </tbody>

                </table>


                <div class="form-control-main-itemMng">
                    {/* <select  >
                        <option value="all"></option>
                        <option value="1d">1</option>
                        <option value="1w">1</option>
                        <option value="1m">1</option>
                        <option value="6m">6</option>
                    </select> */}
                    <select  onChange={HandleOptionOrderState}>
                        <option value="ALL">()</option>
                        <option value="SELL"></option>
                        <option value="SOLD_OUT"></option>
                    </select>
                    <select >
                        <option value="itemNm"></option>
                    </select>

                    <input type="text" placeholder=" "
                    value={search} onChange={handleSearchChange}/>
                    <button id="searchBtn" type="submit" className="btn btn-primary"
                     onClick={handleSearchClick}
                    ></button>
                </div>
            </form>

            <>

            <div>
                <ul className='pagination justify-content-center'>

                <li className='page-item'>
                    <a className='page-link'  disabled={pageNum==0?true:false} 
                    href={'?page='+ (pageNum)} type='button'
                    onClick={prevPage}
                    >Previous
                    </a>
                </li>
                {arr.map((arr) => {
                    return(
                        <>
                    <li className='page-item'>
                            <a className='page-link'
                            href={'?page=' + (arr-1)} 
                            onClick={(e) => clickPage(arr-1, e)}
                            >{arr} 
                            </a>
                        </li>
                        </>
                    )
                })}

                <li className='page-item'>
                    <a className="page-link"  disabled={(pageNum+1)==pageNum?true:false } 
                    href={'?page=' + (pageNum)} type='button'
                    onClick={(e)=>nextPage(pageNum, e)} 
                    >Next
                    </a>
                </li>
            </ul>
            </div>
</>     

            </div>
        );
}

export default ItemMng;