import React, { useState, useEffect } from 'react';
import axios from 'axios'

function ItemDtlTest() {
  //     
  const [ testStr, setTestStr ] = useState('');

  //  
  function callback(str) {
    setTestStr(str);
  }

  //      
  useEffect(
      () => {
        axios({
            url: '/hello',
            method: 'GET'
        }).then((res) => {
            callback(res.data);
        })
      }, []
  );

  return (
      <>
      <h1> {testStr} </h1>
      </>
  );
}

export default ItemDtlTest;